import { BehaviorSubject, Subject, Observable } from "rxjs";

export interface IFleuryHttpEvents {
    onError: Observable<any>;
    isReady: Observable<boolean>;
}

export class FleuryHttpEvents {
    // Event to emit when Oauth fails
    public onError:Subject<any> = new Subject();
    
    // Event to know if client is ready to make request
    // Get the oauth token if ready
    public isReady: BehaviorSubject<boolean> = new BehaviorSubject(false);
}