import axios, { AxiosInstance } from 'axios';
import { IHttpClientService, FleuryHttpResponse } from '../interfaces/ihttp-client';

export class HttpAxios implements IHttpClientService {
    private axiosInstance: AxiosInstance;
    constructor(private baseUrl: string) {
        this.axiosInstance = axios.create({
            baseURL: this.baseUrl
        });
    }

    async post(uri: string, data?: any, headers?: {[key: string]: string}): Promise<FleuryHttpResponse> {
        const response = await this.axiosInstance.post(uri, data, {
            headers,
        });

        const fleuryResponse: FleuryHttpResponse = {
            body: response.data,
            headers: response.headers,
            status: response.status,
            statusText: response.statusText,
        };

        return fleuryResponse;
    }
}
