export interface IOauthService {
    callGrantCode();
    callAccessToken(code: string);
}

export interface IOauthApplication {
    clientId: string;
    secret?: string;
}

export enum OauthFlows {
    GRANT,
    IMPLICIT
}

export interface IOauth {
    application: IOauthApplication;
    flow: OauthFlows;
}

export interface IOauthToken {
    accessToken: string;
    expires: number;
    refreshToken?: string;
}