import { OauthFlows, IOauthToken } from './interfaces/ioauth';
import { IFleuryHttp } from '../../interfaces/ifleury-http';
import { OauthSensediaService } from './implementations/oauth-sensedia-service';
import { IHttpClientService } from '../httpClient/interfaces/ihttp-client';
import { IStorageService } from '../storage/interfaces/istorage';
import { FleuryHttpEvents } from '../events/fleury-http-events';

export class OauthService {
    private oauthProvider: OauthSensediaService;
    private clientId: string;
    private static MILLISECONDS: number = 1000;
    private currentToken: IOauthToken;

    constructor(
        private config: IFleuryHttp,
        private fleuryHttpEvents: FleuryHttpEvents,
        private httpClientService: IHttpClientService,
        private storageService: IStorageService
    ) {
        this.oauthProvider = new OauthSensediaService(this.config, this.httpClientService);
        this.clientId = this.config.oauth.application.clientId;
        const token = this.storageService.get(this.clientId);
        if (token) this.currentToken = JSON.parse(token);

        if (this.config.lazyAuhtorization) {
            this.fleuryHttpEvents.isReady.next(true);
        } else {
            if (!this.currentToken) this.refreshToken();
            else this.fleuryHttpEvents.isReady.next(true);
        }
    }

    private async grantFlow(): Promise<string> {
        const code = await this.oauthProvider.callGrantCode();
        const response = await this.oauthProvider.callAccessToken(code);
        
        this.currentToken = {
            accessToken: response.access_token,
            expires: new Date().getTime() + (response.expires_in * OauthService.MILLISECONDS),
            refreshToken: response.refresh_token
        };

        console.log(this.currentToken);

        this.storageService.set(this.clientId, JSON.stringify(this.currentToken));

        return this.currentToken.accessToken;
    }

    private async implicitFlow(): Promise<any> {
        const response = await this.oauthProvider.callAccessTokenImplicit();

        this.currentToken = {
            accessToken: response.access_token,
            expires: new Date().getTime() + (response.expires_in * OauthService.MILLISECONDS),
        };

        this.storageService.set(this.clientId, JSON.stringify(this.currentToken));

        return this.currentToken.accessToken;
    }

    public isTokenValid(): boolean {
        return this.currentToken ? new Date().getTime() < this.currentToken.expires : false;
    }

    public getAccessToken(): string {
        return this.currentToken.accessToken;
    }

    public getApplicationId(): string {
        return this.clientId;
    }

    refreshToken(): void {
        let promise: Promise<any> = null;
        this.fleuryHttpEvents.isReady.next(false);
        
        switch (this.config.oauth.flow) {
            case OauthFlows.IMPLICIT:
                promise = this.implicitFlow();
                break;
            case OauthFlows.GRANT:
            default:
                promise = this.grantFlow();
                break;
        }

        promise.then(() => {
            this.fleuryHttpEvents.isReady.next(true);
        }).catch((err) => {
            this.fleuryHttpEvents.onError.next(err);
        });
    }
}
