import { IFleuryHttp, IInstance, InstanceType } from '../../interfaces/ifleury-http';
import { AxiosInterceptor } from './implementations/interceptor-axios';
import { FleuryHttpEvents } from '../events/fleury-http-events';
import { OauthService } from '../oauth/oauth-service';

export class InterceptorService {

    private axiosInterceptor: AxiosInterceptor;

    constructor(
        private config: IFleuryHttp,
        private fleuryHttpEvents: FleuryHttpEvents,
        private ouathService: OauthService
    ) {
        this.axiosInterceptor = new AxiosInterceptor(this.config, this.fleuryHttpEvents, this.ouathService);
        if (Array.isArray(this.config.instances)) this.config.instances.forEach(i => this.handleInstance(i));
    }

    private handleInstance(instance: IInstance) {
        switch (instance.type) {
            case InstanceType.Axios:
            default:
                this.axiosInterceptor.handle(instance.value);
        }
    }

    private checkIfInstanceAlreadyExists(instance: IInstance): boolean {
        const found = this.config.instances.find((i: IInstance) => {
            return i.value === instance.value;
        });

        return !!found;
    }

    addInstance(instance: IInstance): void {
        if (!Array.isArray(this.config.instances)) this.config.instances = [];
        if (this.checkIfInstanceAlreadyExists(instance)) return;
        this.handleInstance(instance);
        this.config.instances.push(instance);
    }
}