import { IOauthService, IOauthApplication } from '../interfaces/ioauth';
import { IFleuryHttp } from '../../../interfaces/ifleury-http';
import { IHttpClientService } from '../../httpClient/interfaces/ihttp-client';
import { extractCode } from '../utils/extract-code';
import { toBase64 } from '../utils/base64';

export class OauthSensediaService implements IOauthService {
    private appConfig: IOauthApplication;
    
    constructor(private config: IFleuryHttp, private http: IHttpClientService) {
        this.appConfig = this.config.oauth.application;
    }

    async callGrantCode(): Promise<string> {
        const response = await this.http.post(
            '/oauth/grant-code',
            {
                client_id: this.appConfig.clientId,
                redirect_uri: `${this.config.baseUrl}/oauth/access-token`,
            },
        );

        if (response.status === 201) {
            const code = extractCode('?code=', response.body.redirect_uri);
            return code;
        } else {
            throw new Error('Unable to call grant');
        }
    }

    async callAccessToken(code: string): Promise<any> {
        const key: string = `${this.appConfig.clientId}:${this.appConfig.secret}`;
        const authorization: string = `Basic ${toBase64(key)}`;
        
        const response = await this.http.post(
            '/oauth/access-token',
            {
                grant_type: 'authorization_code',
                code,
            },
            {
                client_id: this.appConfig.clientId,
                Authorization: authorization,
            },
        );

        if (response.status === 201) {
            return response.body
        } else {
            throw new Error('Unable to call access token');
        }
    }

    async callAccessTokenImplicit(): Promise<any> {
        const response = await this.http.post(
            '/oauth/access-token',
            {
                client_id: this.appConfig.clientId,
                grant_type: 'implicit',
                recaptcha: true,
                redirect_uri: `${this.config.baseUrl}/oauth/access-token`
            }
        );

        if (response.status === 201) {
            response.body.access_token = extractCode('?access_token=', response.body.redirect_uri);
            return response.body;
        } else {
            throw new Error('Unable to call access token');
        }
    }
}
