import { IFleuryHttp } from '../../interfaces/ifleury-http';
import { StorageType, IStorageService } from './interfaces/istorage';
import { StorageLocal } from './implementations/storage-local';
import { StorageSession } from './implementations/storage-session';
import { StorageMemory } from './implementations/storage-memory';

const providers: any = {};

export class StorageFactory {
    public static createStorageService(config: IFleuryHttp): IStorageService {
        switch(config.storageType) {
            case StorageType.Memory:
                return new StorageMemory();
            case StorageType.SessionStorage:
                return new StorageSession();
            case StorageType.LocalStorage:
            default:
                return new StorageLocal();
        }
    }
}