import { IFleuryHttp, IInstance, IFleuryHttpConnector } from './interfaces/ifleury-http';
import { IHttpClientService } from './features/httpClient/interfaces/ihttp-client';
import HttpClientFactory from './features/httpClient/http-client-factory';
import { OauthService } from './features/oauth/oauth-service';
import { StorageFactory } from './features/storage/storage-factory';
import { InterceptorService } from './features/interceptors/interceptor-service';
import { FleuryHttpEvents, IFleuryHttpEvents } from './features/events/fleury-http-events';
import { IStorageService } from './features/storage/interfaces/istorage';

export class FleuryHttp {
    public fleuryHttpEvents: FleuryHttpEvents = new FleuryHttpEvents();

    private httpClientService: IHttpClientService;

    private storageService: IStorageService;

    public oauthService: OauthService;

    public interceptorService: InterceptorService;

    constructor(private config: IFleuryHttp) {
        this.httpClientService = HttpClientFactory.createHttpService(this.config);
        this.storageService = StorageFactory.createStorageService(this.config);
        
        this.oauthService = new OauthService(
            this.config,
            this.fleuryHttpEvents,
            this.httpClientService,
            this.storageService
        );

        this.interceptorService = new InterceptorService(
            this.config,
            this.fleuryHttpEvents,
            this.oauthService
        );
    }
}

const FleuryHttpSingleton = (function(){
    let fleuryhttpConnector: IFleuryHttpConnector;
    
    let instance: FleuryHttp = null;

    let events: IFleuryHttpEvents;

    return function(fleuryHttp) {
        if (instance) return fleuryhttpConnector;
        
        instance = fleuryHttp;

        if (!instance) return null;

        events = {
            isReady: instance.fleuryHttpEvents.isReady.asObservable(),
            onError: instance.fleuryHttpEvents.onError.asObservable()
        };

        fleuryhttpConnector = {
            addInstance: (httpInstance: IInstance) => {
                instance.interceptorService.addInstance(httpInstance);
            },
            events: events
        }

        return fleuryhttpConnector;
    };
})();

export class FleuryHttpFactory {
    public static createFleuryHttp(config: IFleuryHttp): IFleuryHttpConnector {
        if (!FleuryHttpSingleton(null)) {
            FleuryHttpSingleton(new FleuryHttp(config));
        }

        return FleuryHttpSingleton(null);
    }
}