import { FleuryInterceptorRequest } from '../interfaces/iintercetors';
import { filter, take, subscribeOn } from 'rxjs/operators';

const insertBaseUrl = async (req: FleuryInterceptorRequest): Promise<FleuryInterceptorRequest> => {
    return new Promise((resolve) => {
        req.url = req.url.indexOf(req.baseUrl) === -1 ? req.baseUrl + req.url : req.url;
        resolve(req);
    });
}

// This function refresh the token if necessary
const isTokenValid = async (req: FleuryInterceptorRequest): Promise<FleuryInterceptorRequest> => {
    return new Promise((resolve) => {
        req.fleuryHttpEvents.isReady
        .pipe(
            filter(value => value),
            take(1)
        ).subscribe(() => {
            if (!req.oauthService.isTokenValid()) req.oauthService.refreshToken();
            resolve(req);
        });
    });
}

// Se dominio eh o mesmo do gateway entao continuar interceptor
const insertAccessToken = async (req: FleuryInterceptorRequest): Promise<FleuryInterceptorRequest> => {
    return new Promise((resolve) => {
        req.fleuryHttpEvents.isReady
        .pipe(
            filter(value => value),
            take(1),
        ).subscribe(() => {
            req.headers['access_token'] = req.oauthService.getAccessToken();
            req.headers['client_id'] = req.oauthService.getApplicationId();
            resolve(req);
        });
    });
};

export const preModifiers: Array<(req: FleuryInterceptorRequest) => Promise<FleuryInterceptorRequest>> = [
    insertBaseUrl,
    isTokenValid,
    insertAccessToken
];