import { AxiosInstance, AxiosRequestConfig, AxiosResponse } from 'axios';
import { IInterceptorService } from '../interfaces/iintercetors';
import { FleuryHttpEvents } from '../../events/fleury-http-events';
import { OauthService } from '../../oauth/oauth-service';
import { IFleuryHttp } from '../../../interfaces/ifleury-http';
import { FleuryInterceptorRequest } from '../interfaces/iintercetors';

import { preValidators } from '../pre/validators';
import { preModifiers } from '../pre/modifiers';
import { posValidators } from '../pos/validators';

export class AxiosInterceptor implements IInterceptorService {
    constructor(
        private config: IFleuryHttp,
        private fleuryHttpEvents: FleuryHttpEvents,
        private oauthService: OauthService
    ) {}

    requestTransformation(httpRequest: AxiosRequestConfig): FleuryInterceptorRequest {
        const hasBaseUrl = httpRequest.url.indexOf(this.config.baseUrl);
        const addBaseUrl = hasBaseUrl === -1 ? this.config.baseUrl : '';
        
        return {
            baseUrl: this.config.baseUrl,
            headers: httpRequest.headers,
            url: addBaseUrl + httpRequest.url,
            body: httpRequest.data,
            fleuryHttpEvents: this.fleuryHttpEvents,
            oauthService: this.oauthService
        };
    }

    handle(instance: AxiosInstance) {
        instance.interceptors.request.use((req) => this.preValidators(req));
        instance.interceptors.response.use((req) => this.posValidators(req));
    }

    private async preModifiers(httpRequest: AxiosRequestConfig): Promise<AxiosRequestConfig> {
        let req = this.requestTransformation(httpRequest);
        
        for (let fn of preModifiers) {
            req = await fn(req);
        }

        httpRequest.headers = req.headers;
        httpRequest.data = req.body;
        httpRequest.url = req.url;

        return httpRequest;
    }

    private async preValidators(httpRequest: AxiosRequestConfig): Promise<AxiosRequestConfig> {
        const req = this.requestTransformation(httpRequest);

        const error = preValidators.find(fn => {
            return fn(req) === false;
        });

        return error ? httpRequest : this.preModifiers(httpRequest)
    }

    private async posValidators(httpResponse: AxiosResponse): Promise<AxiosResponse> {
        posValidators.forEach((fn) => {
            fn({
                oauthService: this.oauthService,
                status: httpResponse.status
            });
        });

        return httpResponse;
    }
}