import { FleuryInterceptorResponse } from '../interfaces/iintercetors';

const verifyResponseStatus = (res: FleuryInterceptorResponse): boolean => {
    if (res.status === 401) res.oauthService.refreshToken();
    return true;
};

export const posValidators: Array<(res: FleuryInterceptorResponse) => boolean> = [
    verifyResponseStatus
];